.hero {
  height: 100vh;
  min-height: 680px;
  max-height: 800px;

  // background-image: linear-gradient(rgba(18, 18, 18, 0.88), rgba(18, 18, 18, 0.88)),
  //   url(../images/hero.jpg);
  // background-repeat: no-repeat;
  // background-size: cover;
  // background-position: center;
}
.hero__container {
  padding-top: 180px;
  max-width: 500px;
  margin: 0 auto;
}
.hero__title {
  font-size: 38px;
  margin-bottom: 37px;
  color: #fff;
}
.hero__deskription {
  font-size: 22px;
  line-height: 1.23;
}
.hero__btn-list {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 70px;
}
// .hero__btn-item {
//   &:not(:last-child) {
//     margin-right: 10px;
//   }
// }
.hero__btn {
  padding: 16px 16px;
  border: 1px solid #fff;
  border-radius: 10px;
  background: none;
  color: #fff;
  font-size: 14px;
}
.active {
  border: 2px solid #bef527;
  color: #bef527;
}
@media screen and (min-width: 400px) {
  .hero__container {
    padding-top: 220px;
  }
  .hero__title {
    font-size: 45px;
    line-height: 1.14;
    margin-bottom: 40px;
  }
  .hero__deskription {
    font-size: 24px;
    margin-bottom: 100px;
  }
  .hero__btn-list {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  // .hero__btn-item {
  //   &:not(:last-child) {
  //     margin-right: 10px;
  //   }
  // }
  .hero__btn {
    padding: 18px 16px;
    font-size: 18px;
  }
}
@media screen and (min-width: 768px) {
  .hero__container {
    padding-top: 250px;
    max-width: 650px;
    text-align: center;
  }
  .hero__title {
    font-size: 60px;

    margin-bottom: 40px;
  }
  .hero__deskription {
    font-size: 28px;
    margin-bottom: 120px;
  }
  .hero__btn-list {
    justify-content: center;
    font-size: 22px;
  }
  .hero__btn-item {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }
}
