// 768+
@media screen and (min-width: 768px) {
  .container {
    width: 738px;
  }
}
// 1024+
@media screen and (min-width: 1024px) {
  .container {
    width: 994px;
  }
}
@media screen and (min-width: 1440px) {
  .container {
    width: 1410px;
  }
}
